.nav {
  &__burger {
    display: none;
    @include media(smartphone) {
      display: inline-block;
      position: relative;
      width: em(30, $fs);
      height: em(16, $fs);

      &:before {
        margin-top: -7px;
      }

      &:after {
        margin-top: 5px;
      }

      &:before,
      &:after {
        content: '';
        position: absolute;
        transition: transform .3s ease-in-out;
      }

      span,
      &:before,
      &:after {
        width: 100%;
        height: 2px;
        display: block;
        background-color: $primary;
      }

      &--active {
        width: em(30, $fs);
        height: em(16, $fs);

        span {
          display: none;
        }

        &:before {
          transform: rotate(45deg);
        }

        &:after {
          transform: rotate(-45deg);
        }

        &:before,
        &:after {
          margin-top: 0;
        }
      }
    }
  }

  &__list {
    line-height: em(26, $fs);
  }

  @include media(smartphone) {
    line-height: 0;
  }
}

.list {
  &__item {
    $fs: 18;
    font-size: em($fs, $fs_base);

    &:not(:last-child) {
      margin-right: em(20, $fs);
    }

    @include media(smartphone) {
      width: 100%;

      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }

  &__link {
    padding: 0;

    &:before {
      bottom: em(-10, $fs);
    }

    @include media(smartphone) {
      $fs: 18;
      display: flex;
      width: 100%;
      justify-content: center;
      padding: em(10, $fs);
      border: 1px solid $secondary;
      transition: background-color .3s ease-in-out;

      &:hover,
      &--active {
        background-color: $secondary;
        border: 1px solid $eighty;
      }

      &:before {
        display: none;
      }
    }
  }

  @include media(smartphone) {
    &--active {
      left: 0;
    }
  }
}

footer {
  .nav {
    width: 100%;

    &__burger {
      display: none;
    }

    &__list {
      line-height: 0;
    }
  }

  .list {
    position: static;
    display: inline-flex;
    flex-direction: column;
    background-color: transparent;
    justify-content: space-around;
    align-items: flex-start;
    @include media(smartphone) {
      flex-direction: row;
    }

    &__item {
      width: auto;

      &:not(:last-child) {
        margin-bottom: em(30, $fs);
        margin-right: 0;
      }

      @include media(smartphone) {
        $fs: 14;
        font-size: em($fs, $fs_base);

        &:not(:last-child) {
          margin-bottom: 0;
          margin-right: em(10, $fs);
        }
      }
      @include media(mobile) {
        $fs: 12;
        font-size: em($fs, $fs_base);
      }
    }

    &__link {
      display: inline-block;
      width: auto;
      border: 0;
      padding: 0;
      line-height: 1;
      color: $primary;
      transition: color .3s ease-in-out;

      &:hover,
      &--active {
        color: $thirdly;
      }

      &:hover {
        background-color: transparent;
        border: 0;
      }

      &:before {
        display: none;
      }
    }
  }
}